.link {
    display: inline-block;
    text-decoration: none;
    vertical-align: top;
}
.icon {
    height: 20px;
    margin-top: 2px;
    width: 20px;
}

.selected {
    fill: var(--colors-white);
}
.unselected {
    fill: var(--colors-grey700);
}

@media only screen and (max-width: 480px) {
    .link {
        margin: 0 -2px;
    }
}
